<template>
    <v-container class="noticias__container">

        <div class="noticias">
            <div class="content">
                <div class="title">

                    <h1 class="text-justify">Noticias Terapia</h1>
                    <p 
                    class="text-justify text--secondary" style="padding: 0px 13px 0px 13px"
                    >
                    
                    Infórmate de todas nuestras últimas noticias,  para que estés al tanto de todos nuestros objetivos</p>
                    <v-btn color="success text-capitalize" to="/noticias">Ver todas</v-btn>
                </div>
                <div class="elements">
                    

                    <card-noticias class="card_not"
                    v-for="(noticia, index) in getNoticiasPaginadas"
                    :key="index"
                    v-bind:value="{
                            Titulo: noticia.Titulo,
                            Texto: noticia.Texto,
                            Imagen: noticia.Imagen
                    }"
                    >
                    </card-noticias>

                    
                </div>
            </div>
        </div>
        <div class="buttons">
            <v-btn
            :disabled = isDisabledBack
            @click="page--"
            color="success"
            >
            <v-icon>mdi-chevron-left</v-icon>
            </v-btn>

            <v-btn
            :disabled = isDisabledForward
            @click="page++"
            color="success"
            >
            <v-icon>mdi-chevron-right</v-icon>
            </v-btn>
        </div>
    </v-container>
</template>


<script>

import {mapState} from 'vuex';

export default {
    name: 'Noticias',
    components:{
        CardNoticias: () => import('@/components/base/CardNoticias')
    },
    data(){
        return{
            page: 1,
            itemsPerPage: 3,
            backBtnStatus: false,
            forwardBtnStatus: false
        }
    },
    computed:{
        ...mapState(['noticias']),
        isDisabledBack(){
            if(this.page === 1){
                return true;
            }
            return false;
        },
        isDisabledForward(){
            if(this.page  === this.getTotalPaginas){
                return true;
            }
            return false;
        },
        getTotalPaginas(){
           return  Math.ceil(this.noticias.length / this.itemsPerPage)
        },

        getNoticiasPaginadas() {
            const start = (this.page - 1) * this.itemsPerPage
            const stop = this.page * this.itemsPerPage
            return this.noticias.slice(start, stop)
      },

    },
    methods:{
        printStatus(){
            console.log(this.forwardBtnStatus);
            console.log(this.page);
            console.log(this.getTotalPaginas);
            console.log(this.getNoticiasPaginadas);
            console.log(this.noticias);
        }
    }

}
</script>



<style>



.noticias__container .noticias .content{
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    padding: 10px;
}

.noticias__container .noticias .content .title{
    display: flex;
    flex-direction: column;
    flex: 1 6 auto;
    align-items: center;
    font-size: 16px !important;
    padding: 50px 30px 50px 30px ;
}

.noticias__container .buttons{
    padding: 10px;
}

.content .title p{
margin-top: 20px;
line-height: 1.5;
}

.content .title .v-btn{
    width: 200px;
}

.noticias__container .content .elements{
    display: flex;
    flex: 5 6 auto;
    justify-content: center;
    flex-direction: row;

}
.noticias__container .buttons{
    display: flex;
    justify-content: flex-end;
}

.noticias__container .content .v-btn{
    border-radius: 20px !important;
}

.card_not{
    margin: 10px;
}


@media (max-width: 1108px){
.noticias__container .noticias .content{
   flex-direction: column;
}

.noticias__container .noticias .content .elements{
   flex-direction: column;
}

.noticias__container .noticias .content .title{
    
       padding: 50px 50px 20px 50px;
}

}


</style>